import { useState } from 'react';
import { Link } from 'react-router-dom';

function Nav (){
    const [activeButton, setActiveButton] = useState(0);
  
    const handleButtonClick = (index) => {
    setActiveButton(index);
    };
  return(
    
    <nav>
   <Link to="/"><button className={activeButton === 1 ? 'navbutton active' : 'navbutton'} onClick={() => handleButtonClick(1)}>Chat-GPT</button></Link>
   <Link to="/chekg"><button className={activeButton === 0 ? 'navbutton active' : 'navbutton'} onClick={() => handleButtonClick(0)}>Chek-GPT</button></Link>

    </nav>
  );
}
export default Nav;