import { useState, useEffect } from 'react';
import './assets/Chat.css';
import TypeIt from 'typeit-react';
// Logo Import
import logo from './assets/logo.png';
// import { Link } from 'react-router-dom';

import Nav from './page/Nav';
import Footer from './page/Footer';


function ChekG() {
  const [soalText, setSoal] = useState('');
  const [jawabanText, setJawabanText] = useState('');
  const [generatedText, setGeneratedText] = useState('');
  const [hasil, setHasil] = useState('');
  const [nilai, setNilai] = useState('');
  const [loading, setLoading] = useState(false);
 
  
  const hasilJawaban = async () => {
    setLoading(true);
    
    const {Configuration, OpenAIApi} = require ("openai");

    const configuration = new Configuration({
      apiKey: process.env.REACT_APP_OPENAI_API_KEY,
    });
    const openai = new OpenAIApi(configuration);

    const prompt = [
      `Soal: ${soalText},
      Jawaban Essay: ${jawabanText},
      Apakah Jawaban Essay sudah benar?, jika Jawaban Essay benar maka awali jawaban dengan kata "Benar", jika salah awali Jawaban Essay dengan kata "Salah" lalu tuliskan jawaban yang benar`
    ];

    const response = await openai.createCompletion({
      model: "text-davinci-003",
      prompt: prompt,
      temperature: 0.7,
      max_tokens: 1205
    });

    const generatedText = response.data.choices[0].text.trim();
    setGeneratedText(generatedText);
    setLoading(false);
  };

  useEffect(() => {
    if(generatedText.includes("Benar", "Sudah benar", "Sudah benar","benar" )) {
      setHasil('Benar');
      setNilai(10);
    }
    else if (generatedText.includes("Salah", "salah")) {
      setHasil('Salah');
      setNilai(0);
    }
    else {
      setHasil('Silahkan isi jawaban anda');
      setNilai(0);
    }
  }, [generatedText]);
return (
  <div className='App'>
     <Nav/>
    <img src={logo} className='App-logo pt' alt="logo" />
    <h1>
  <TypeIt
    options={{
      strings: 'My Chek-GPT Essay',
      speed: 50,
      waitUntilVisible: true,
    }}
  />
</h1>
  <div cassName='container-wrapper'>
    <div>
    <label>Soal :</label>
    <textarea
    placeholder='Masukan Soal'
    onChange={(event)=> setSoal (event.target.value)}
    />
    </div>
    <div>
      <label>Jawaban Essay: </label>
      <textarea
    placeholder='masukan jawaban Essay'
    value={jawabanText}
    onChange={(event)=> setJawabanText (event.target.value)}
    />
    </div>
    <div>
    {loading ? (
      <button className='navbutton' disabled> <i className='fa fa-spinner fa-spin'></i>Loading</button>
      ) : (
        <button className='navbutton' onClick={hasilJawaban}>Cek Jawaban</button>
        )}
    </div>
 {/* <button onClick={hasilJawaban}>Cek Jawaban</button> */}
    {hasil && (
      <div className='pb'>
        <h2>Hasil :</h2>
        <p>Jawaban Anda {hasil} ({nilai})</p>
      </div>
    )}

    {generatedText && (
      <div className='pb'>
        <h2>Koreksi :</h2>
        {/* <p>{generatedText}</p> */}
        <div className='container'>
          <p>
          <TypeIt
          key={generatedText}
              options={{
                strings: generatedText,
                speed: 50,
                waitUntilVisible: true,
              }}
            />
            </p>
            </div>
      </div>
    )}
    <Footer></Footer>
  </div>
</div>
);
}  

export default ChekG;